<form class="form_wrapper" :class="[color]" @submit.prevent="addNewTask"><!-- Button for add new task--><button class="add_btn bg_color main_color" type="submit" :disabled="title === ''">+</button><input class="input" v-model="title" placeholder="Add a new task" type="text" name="new_task_title" id="new_task_title"></form>