
// Vue
import { defineComponent, ref } from 'vue'
import { useStore } from 'vuex'
// Types
import { Task } from '@/types/todos/interfaces'
import { TodosActionsEnum } from '@/types/todos/types'
// Helpers
import { GetCurrentDate, GetCurrentDateTime } from '@/helpers/date'

export default defineComponent({
  name: 'TaskForm',
  props: {
    /**
     * Category ID
     */
    categoryId: {
      type: Number,
      required: false,
      default: null
    },
    /**
     * Color
     */
    color: {
      type: String,
      required: false,
      default: 'pink'
    }
  },
  setup (props) {
    /**
     * Context
     */
    const store = useStore()
    /**
     * Data
     */
    // Form title
    const title = ref<string>('')
    /**
     * Methods
     */
    // # Add new task
    const addNewTask = (): void => {
      // News task params
      // # ID
      const id = Number(Date.now().toLocaleString().replaceAll(',', ''))
      // News task object
      const newTask: Task = {
        id: id,
        parent_id: null,
        category_id: props.categoryId,
        title: title.value,
        completed: false,
        target_date: GetCurrentDate(),
        created_at: GetCurrentDateTime(),
        deleted_at: null
      }

      store.dispatch(TodosActionsEnum.CreateTask, newTask)
      title.value = ''
    }

    return {
      // Data
      title,
      // Methods
      addNewTask
    }
  }
})
