<div class="task_wrapper" v-if="todos.length &gt; 0 &amp;&amp; emptyView === false"><h2 class="title">{{ title }}{{ taskLengthView === true ? ` – ${todos.length}` : '' }}</h2><div class="content"><TaskComponent v-for="(todo, key) in todos" :key="key" :task="todo" :color="color"></TaskComponent></div></div>